import React, { useEffect } from 'react';
import ReactGA from 'react-ga';
import './App.css';
import linkedin from './linkedin.svg'
import github from './github.svg'
import mail from './mail.svg'

const App = () => {
  useEffect(() => {
    ReactGA.pageview(window.location.pathname + window.location.search);
  }, []);

  return (
    <div className="App">
      <header className="App-header">
        <p className="App-text">
          We'll Be Right Back
        </p>
        <div>
          <ReactGA.OutboundLink
            eventLabel="GitHub"
            to="https://github.com/karlobathan"
          >
            <img src={github} className="App-logo" alt="github icon" />
          </ReactGA.OutboundLink>
          <ReactGA.OutboundLink
            eventLabel="LinkedIn"
            to="https://www.linkedin.com/in/karlo-bathan"
          >
            <img src={linkedin} className="App-logo" alt="linkedin icon" />
          </ReactGA.OutboundLink>
          <ReactGA.OutboundLink
            eventLabel="GitHub"
            to="mailto:karlobathan.dev@gmail.com"
          >
            <img src={mail} className="App-logo" alt="mail icon" />
          </ReactGA.OutboundLink>
        </div>
      </header>
    </div>
  );
}

export default App;
